* {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  background-color: rgb(198, 235, 235);
}
.container {
  max-width: 1000px;
  margin: auto;
  margin-top: 70px;
  text-align: center;
  padding: 15px;
}
.container h1 {
  text-transform: uppercase;
  font-weight: 600;
  color: rgb(31, 29, 29);
}
.formcontainer {
  width: 100%;
  margin-top: 15px;
}
.formcontainer label {
  margin: 10px;
  font-size: 25px;
  font-weight: 400;
}
.formcontainer input {
  width: 100px;
  padding: 5px 10px;
  border-radius: 5px;
  border-color: transparent;
  font-size: 16px;
}
.btn {
  margin: 10px;
  text-transform: capitalize;
  border-color: transparent;
  background-color: rgb(15, 155, 155);
  font-size: 17px;
  font-weight: 400;
  padding: 5px 10px;
  border-radius: 5px;
  letter-spacing: 2px;
  color: rgb(8, 36, 36);
}
.btn:hover {
  color: rgb(6, 31, 31);
  background-color: rgb(17, 190, 190);
}
.loremContainer {
  max-width: 800px;
  margin: auto;
  margin-top: 50px;
  font-size: 17px;
  color: rgb(54, 54, 54);
}
.loremContainer p {
  margin-top: 30px;
}

@media only screen and (max-width: 500px) {
  .container h1 {
    text-transform: uppercase;
    font-weight: 600;
    font-size: 20px;
    color: rgb(31, 29, 29);
  }
  .formcontainer label {
    margin: 10px;
    font-size: 20px;
    font-weight: 400;
    margin-bottom: 20px;
  }
}
